import React from 'react';
import styled from 'styled-components';
import { Banner } from './homeStyledComponents';
import { colors, mobileThresholdPixels } from '../styledComponents';
import photo from '../../assets/Josephine.jpg';

const Container = styled.div`
  position: relative;
`;

const InvertedBlock = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px 0px;
  background-color: ${colors.lightGrey};

  @media (max-width: ${mobileThresholdPixels}) {
    flex-direction: column-reverse;
    padding: 2px 0px;
  }
`;

const Column = styled.div`
  margin: 20px 20px 80px;
  max-width: 45%;
  display: flex;
  flex-direction: column;
  text-align: justify;

  @media (max-width: ${mobileThresholdPixels}) {
    margin: 5px 15px;
    max-width: 100%;
  }
`;

const P = styled.p`
  color: ${colors.navy};
`;

const Title = styled.h3`
  color: ${colors.navy};
  margin: 0px 5px;
`;

const Img = styled.img`
  object-fit: cover;
  height: 400px;
`;

const BannerContainer = styled.div`
  position: absolute;
  bottom: 40px;
  @media (max-width: ${mobileThresholdPixels}) {
    position: static;
  }
`;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const HowItWorks = () => (
  <Container>
    <InvertedBlock>
      <Column>
        <Row>
          <img src="https://icon.now.sh/home/32/41465D" alt="chevron icon" />
          <Title> domicile</Title>
        </Row>
        <P>
          {"Votre couturier se dplace jusqu' chez vous, dans Paris et la proche banlieue,"}
          {' tous les jours de 17.00  21.30..'}
        </P>
        <P>  </P>
        <Row>
          <img
            src="https://pro.axa.fr/content/dam/axa-pro/homepage/AXA_Logo.png" height="32px" width="32px"
            alt="chevron icon"
          />
          <Title>Assurance</Title>
        </Row>
        <P>{'Toutes les commandes passes sur notre plateforme sont assures par notre partenaire Axa.'}</P>
        <P>  </P>
        <Row>
          <img src="https://icon.now.sh/sentiment_very_satisfied/32/41465D" alt="chevron icon" />
          <Title>Qualit</Title>
        </Row>
        <P>{'Votre couturier est un artisan professionnel, choisi avec soin par tilli.'}</P>
      </Column>
      <Column>
        <Img src={photo} alt="" />
      </Column>
    </InvertedBlock>
    <BannerContainer><Banner /></BannerContainer>
  </Container>
);

export default HowItWorks;



