/* eslint-disable global-require */
import React from 'react';
import { Route, IndexRoute } from 'react-router';
import AppContainer from './pages/common/AppContainer';
import Order from './pages/common/Order';
import Home from './pages/home/Home';
import BeTillistePage from './pages/home/BeTillistePage';

// require.ensure polyfill for node
if (typeof require.ensure !== 'function') {
  require.ensure = function requireModule(deps, callback) {
    callback(require);
  };
}

/* Workaround for async react routes to work with react-hot-reloader till
  https://github.com/reactjs/react-router/issues/2182 and
  https://github.com/gaearon/react-hot-loader/issues/288 is fixed.
 */
if (process.env.NODE_ENV !== 'production') {
  // Require async routes only in development for react-hot-reloader to work.
  require('./pages/order/Step1Container');
  require('./pages/order/Step2Container');
  require('./pages/order/Step3Container');
  require('./pages/order/Step4');
  require('./pages/order/Step5');
  require('./pages/home/BeTillistePage');
  require('./pages/home/Home');
}

// react-router setup with code-splitting
// More info: http://blog.mxstbr.com/2016/01/react-apps-with-pages/
export default (
  <Route path="/">
    <IndexRoute component={Home} />
    <Route path="/devenir-couturier" component={BeTillistePage} />
    <Route path="/order" component={AppContainer}>
      <Route component={Order}>
        <Route
          path="/order/step1"
          getComponent={(nextState, cb) => {
            require.ensure([], (require) => {
              cb(null, require('./pages/order/Step1Container').default);
            });
          }}
        />
        <Route
          path="/order/step2"
          getComponent={(nextState, cb) => {
            require.ensure([], (require) => {
              cb(null, require('./pages/order/Step2Container').default);
            });
          }}
        />
        <Route
          path="/order/step3"
          getComponent={(nextState, cb) => {
            require.ensure([], (require) => {
              cb(null, require('./pages/order/Step3Container').default);
            });
          }}
        />
        <Route
          path="/order/step4"
          getComponent={(nextState, cb) => {
            require.ensure([], (require) => {
              cb(null, require('./pages/order/Step4').default);
            });
          }}
        />
        <Route
          path="/order/step5"
          getComponent={(nextState, cb) => {
            require.ensure([], (require) => {
              cb(null, require('./pages/order/Step5').default);
            });
          }}
        />
      </Route>
    </Route>
  </Route>
);



