import React from 'react';
import { Link } from 'react-router';
import styled from 'styled-components';
import { TextLink } from './homeStyledComponents';
import logo from '../../assets/logo1.png';

export const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin: 10px 0px;
`;

const MenuBar = () => (
  <Row>
    <Link to={'/'}><TextLink><img src={logo} alt="logo" width="80" height="80" /></TextLink></Link>
    <Link to={'/devenir-couturier'} style={{ textDecoration: 'none' }}>
      <TextLink href="">
        devenez couturier
      </TextLink>
    </Link>
    <Link><TextLink href="#MobileApps">{'me connecter'}</TextLink></Link>
  </Row>
);

export default MenuBar;



