import React, { PropTypes } from 'react';
import styled from 'styled-components';
import bounds from 'react-bounds';
import { mobileThreshold, mobileThresholdPixels } from '../../components/styledComponents';
import { Container, Content, HeaderPhoto }
  from '../../components/Home/homeStyledComponents';
import MenuBar from '../../components/Home/MenuBar';
import TheWhy from '../../components/BeTilliste/TheWhy';
import TheHow from '../../components/BeTilliste/TheHow';
import TheWhat from '../../components/BeTilliste/TheWhat';
import TillisteFeedbacks from '../../components/BeTilliste/TillisteFeedbacks';
import SocialNetworks from '../../components/Home/SocialNetworks';
import Footer from '../../components/Home/Footer';
import headerPhoto from '../../assets/tilliste.jpg';

const Title = styled.h1`
  position: absolute;
  bottom: 160px;
  left: 0;
  right: 0;
  color: white;
  font-weight: 200;
  margin: auto;
  font-size: 60px;

  @media (max-width: ${mobileThresholdPixels}) {
    bottom: 80px;
    font-size: 36px;
  }
`;

const HeaderPhotoContainer = styled.div`
  position: relative;
  text-align: center;
`;

class BeTillistePage extends React.Component {
  static bounds() {
    return {
      mobile: {
        minWidth: 0,
        maxWidth: mobileThreshold,
      },
      desktop: {
        minWidth: mobileThreshold + 1,
      },
    };
  }

  render() {
    const { isBound } = this.props;
    return (
      <Container>
        <Content>
          <MenuBar />
          <HeaderPhotoContainer>
            <HeaderPhoto src={headerPhoto} alt="" />
            <Title>{' soyez couturier '}</Title>
          </HeaderPhotoContainer>
          <TheWhy />
          <TheHow />
          <TheWhat />
          <TillisteFeedbacks isMobile={isBound('mobile')} />
          <SocialNetworks />
          <Footer />
        </Content>
      </Container>
    );
  }
}

BeTillistePage.propTypes = {
  isBound: PropTypes.func.isRequired,
};

export default bounds.wrap(BeTillistePage);



