import React, { PropTypes } from 'react';
import styled, { keyframes } from 'styled-components';
import { colors, mobileThresholdPixels } from '../styledComponents';

const Container = styled.div`
  display: inline-block;
  height: 400px;
  width: ${props => props.large ? '480px' : '300px'};
  margin: 0px 20px;
  background-color: white;
  position: relative;

  @media (max-width: ${mobileThresholdPixels}) {
    height: 400px;
    width: calc(100vw - 40px);
  }
`;

const Text = styled.p`
  text-align: justify;
  font-size: 18px;
  color: ${colors.navy};

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 12px;
  }
`;

const P = styled.p`
  color: ${colors.navy};

  @media (max-width: ${mobileThresholdPixels}) {
    margin: 6px 0px;
  }
`;

const Content = styled.div`
  margin: 20px;
  position: absolute;
  bottom: 0;

  @media (max-width: ${mobileThresholdPixels}) {
    position: static;
  }
`;

const fadein = keyframes`
  from { opacity: 0.2; }
  to   { opacity: 1; }
`;

const ImageContent = styled.div`
  opacity: 0;
  background-color: white;
  position: absolute;
  bottom: 0;
  width: calc(100% - 40px);
  padding: 20px;

  &:hover {
    opacity: 1;
    animation: ${fadein} 0.8s;
  }
`;

const Author = styled.p`
  font-size: 12px;
  font-weight: 600;
  color: ${colors.navy};


  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 11px;
  }
`;

const Img = styled.img`
  object-fit: cover;
  height: 400px;
  width: 300px;

  @media (max-width: ${mobileThresholdPixels}) {
    width: calc(100vw - 40px);
  }
`;

const Feedback = ({ text, dataIndex, author, image, large }) => (
  <Container data-index={dataIndex} large={large}>
    {image ?
      <div>
        <Img src={image} alt="" />
        <ImageContent>
          {text && <Text>{text}</Text>}
          {author && <Author>{author}</Author>}
          <P>  </P>
        </ImageContent>
      </div>
      : <Content>
        {text && <Text>{text}</Text>}
        {author && <Author>{author}</Author>}
        <P>  </P>
      </Content>
    }
  </Container>
);

Feedback.propTypes = {
  dataIndex: PropTypes.string.isRequired,
  text: PropTypes.node,
  image: PropTypes.string,
  title: PropTypes.node,
  content: PropTypes.node,
  author: PropTypes.node,
  large: PropTypes.bool,
};

Feedback.defaultProps = {
  text: '',
  image: '',
  title: '',
  content: '',
  author: '',
  large: false,
};

export default Feedback;



