import React from 'react';
import styled from 'styled-components';
import { Icon } from './homeStyledComponents';
import { colors } from '../styledComponents';
import facebook from '../../assets/facebook.svg';
import instagram from '../../assets/instagram.svg';

const Container = styled.div`
  margin: 0px 20px;
  padding: 40px 0px 40px;
`;

const P = styled.p`
  color: ${colors.navy};
  text-align: center;
  font-size: 18px;
`;

const Row = styled.div`
  text-align: center;
`;

const SocialNetworks = () => (
  <Container>
    <P>{"ne perdez pas le fil, suivez toute l'actualit de tilli sur facebook et instagram"}</P>
    <Row>
      <a target="_blank" rel="noreferrer noopener" href="https://www.facebook.com/tilli.fr">
        <Icon src={facebook} alt="" />
      </a>
      <a target="_blank" rel="noreferrer noopener" href="https://www.instagram.com/tillicouture/">
        <Icon src={instagram} alt="" />
      </a>
    </Row>
  </Container>
);

export default SocialNetworks;



