import React, { PropTypes } from 'react';
import styled from 'styled-components';
import Slider from 'react-slick';
import { Title2 } from '../Home/homeStyledComponents';
import { colors } from '../styledComponents';
import Feedback from '../Home/Feedback';

const Container = styled.div`
  padding: 20px 0px 40px;
  background-color: ${colors.lightGrey};
`;

const baseSettings = {
  dots: true,
  infinite: true,
  autoplay: true,
  autoplaySpeed: 4000,
  adaptiveHeight: true,
};

const settingsSmall = {
  ...baseSettings,
  slidesToShow: 1,
  slidesToScroll: 1,
};

const settingsRegular = {
  ...baseSettings,
  slidesToShow: 4,
  slidesToScroll: 2,
};

const feedbacks = [
  {
    id: '0',
    text: `Etre tilliste cest avoir la possibilit dtre toujours une entrepreneure qui dcide de ses missions en
      fonction de ses prfrences et son planning. Pouvoir mler le plaisir de la couture et celui de la rencontre.
      Cest pour moi loccasion davoir un complment de revenu en rapport avec mon mtier de cur, tout en tant
      libre de dvelopper mes autres projets business.`,
    author: 'Daphn',
  },
  {
    id: '1',
    text: `Je me suis inscrite sur Tilli il y a quelques semaines et aprs 3 missions j'ai pu m'acheter une super
    machine industrielle ! Tilli c'est une application qui me permet de proposer mes services de couturire contre une
    rmunration. C'est moi qui tablis mes comptences, ce que je veux et ne veux pas faire et c'est galement moi qui
    choisis d'accepter ou non une mission en fonction de mon emploi du temps !`,
    author: 'Morgane',
  },
  {
    id: '2',
    text: `Je suis contente d'tre avec Tilli, je peux concilier ma vie d'tudiante avec les missions sur Paris, et le
    plus gnial c'est de pratiquer en mme temps que j'apprends ! Tilli m'as permis de financer mes fournitures et
    tissus pour mon projet de collection de fin d'anne. Merci ! Les clients de Tilli sont trs accueillants,
    enthousiastes et nous avons de trs bons changes, je suis contente d'avoir fais leur connaissance.`,
    author: 'Audrey',
  },
];

const TheyLoveTilli = ({ isMobile }) => {
  const settings = isMobile ? settingsSmall : settingsRegular;
  return (
    <Container>
      <Title2> parole de tillistes </Title2>
      <Slider {...settings}>
        {feedbacks.map(feedback =>
          <Feedback
            large
            dataIndex={feedback.id}
            key={feedback.id}
            {...feedback}
          />,
        )}
      </Slider>
    </Container>
  );
};

TheyLoveTilli.propTypes = {
  isMobile: PropTypes.bool.isRequired,
};

export default TheyLoveTilli;



