import React, { PropTypes } from 'react';
import { Background, Container } from '../../components/styledComponents';

const Order = ({ children }) => (
  <div>
    <Background />
    <Container>
      {children}
    </Container>
  </div>
);

Order.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Order;



