import React from 'react';
import styled from 'styled-components';
import { Title2 } from './homeStyledComponents';
import NumerotedBlock from './NumerotedBlock';
import { colors, mobileThresholdPixels } from '../styledComponents';
import heart from '../../assets/heart.svg';
import machine from '../../assets/machine.svg';
import buste from '../../assets/buste.svg';

const Container = styled.div`
  margin: 0px 20px;
  padding: 20px 0px 40px;
`;

const P = styled.p`
  color: ${colors.navy};
`;

const SmallP = styled.p`
  color: ${colors.navy};
  font-size: 12px;
`;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 10px 0px;

  @media (max-width: ${mobileThresholdPixels}) {
    flex-direction: column;
  }
`;

const WhyTilli = () => (
  <Container>
    <Title2> pourquoi tilli </Title2>
    <Row>
      <NumerotedBlock title="Seconde vie" icon={heart}>
        <P>{'Votre placard dborde des vtements que vous aimez, mais que vous ne portez plus.'}</P>
        <P>  </P>
        <SmallP>{'On utilise en moyenne 20% de sa garde-robe.'}
          {'tilli redonne vie aux 80% restant.'}</SmallP>
      </NumerotedBlock>
      <NumerotedBlock title="Mise  taille" icon={buste}>
        <P>{'Vous achetez du prt--porter mais vous n\'avez pas la morphologie de tout le monde.'}</P>
        <P>  </P>
        <SmallP>{'tilli transforme vos achats prt--porter en pices uniques  vos mesures'}</SmallP>
      </NumerotedBlock>
      <NumerotedBlock title="Gain de temps" icon={machine}>
        <P>{'Vous n\'avez pas le temps et encore moins l\'envie de passer  la retoucherie.'}</P>
        <P>  </P>
        <SmallP>{'Votre couturier tilli se dplace jusqu\' votre dressing pour lui redonner vie.'}</SmallP>
      </NumerotedBlock>
    </Row>
  </Container>
);

export default WhyTilli;



