import React, { PropTypes } from 'react';
import bounds from 'react-bounds';
import { Link } from 'react-router';
import { mobileThreshold } from '../../components/styledComponents';
import { Container, Content, HeaderPhoto, ButtonHeaderContainer, Button }
  from '../../components/Home/homeStyledComponents';
import MenuBar from '../../components/Home/MenuBar';
import HowItWorks from '../../components/Home/HowItWorks';
import HowItWorks2 from '../../components/Home/HowItWorks2';
import WhyTilli from '../../components/Home/WhyTilli';
import BeTilliste from '../../components/Home/BeTilliste';
import MobileApps from '../../components/Home/MobileApps';
import TheyLoveTilli from '../../components/Home/TheyLoveTilli';
import SocialNetworks from '../../components/Home/SocialNetworks';
import Footer from '../../components/Home/Footer';
import headerPhoto from '../../assets/photo-header.jpg';

class Home extends React.Component {
  static bounds() {
    return {
      mobile: {
        minWidth: 0,
        maxWidth: mobileThreshold,
      },
      desktop: {
        minWidth: mobileThreshold + 1,
      },
    };
  }

  render() {
    const { isBound } = this.props;
    return (
      <Container>
        <Content>
          <MenuBar />
          <div style={{ position: 'relative' }}>
            <HeaderPhoto src={headerPhoto} alt="" />
            <ButtonHeaderContainer>
              <Link to={'/order/step1'}>
                <Button animated large>{'>> rserver <<'}</Button>
              </Link>
            </ButtonHeaderContainer>
          </div>
          <HowItWorks />
          <HowItWorks2 />
          <WhyTilli />
          <MobileApps />
          <BeTilliste />
          <TheyLoveTilli isMobile={isBound('mobile')} />
          <SocialNetworks />
          <Footer />
        </Content>
      </Container>
    );
  }
}

Home.propTypes = {
  isBound: PropTypes.func.isRequired,
};

export default bounds.wrap(Home);



