import styled, { keyframes } from 'styled-components';
import { colors, mobileThresholdPixels } from '../styledComponents';
import trait from '../../assets/trait.png';

export const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 10px 0px;
`;

export const TextLink = styled.div`
  color: ${colors.navy};
  text-decoration: none;

  &:hover {
    transform: scale(1.1);
  }

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 10px;
    text-align: center;
  }
`;

export const Content = styled.div`
  width: 100%;
  background-color: white;
`;

export const Container = styled.div`
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  background-color: white;
`;

const headerPhotoMargin = 40;
export const HeaderPhoto = styled.img`
  margin: 0px ${headerPhotoMargin}px;
  object-fit: cover;
  height: 800px;
  width: calc(100vw - ${2 * headerPhotoMargin}px);

  @media (max-width: ${mobileThresholdPixels}) {
    margin: 0px;
    height: auto;
    width: 100vw;
    object-fit: unset;
  }
`;

const jump = keyframes`
  from { transform: scale(1); box-shadow: 1px 2px 1px rgba(0, 0, 0, 0.32); }
  to   { transform: scale(1.2); box-shadow: 5px 10px 5px rgba(0, 0, 0, 0.8); }
`;

export const Button = styled.div`
  display: inline-block;
  min-width: 280px;
  text-transform: uppercase;
  ${props => props.inverted ? '' : `border: 3px solid ${colors.navy};`};
  cursor: pointer;
  font-weight: bold;
  font-size: ${props => props.large ? '24px' : '18px'};
  margin: 20px auto;
  padding: ${props => props.large ? '25px 35px' : '25px'};
  letter-spacing: 1px;
  color: ${props => props.inverted ? '#fff' : colors.navy};
  background-color: ${props => props.inverted ? colors.navy : colors.whiteTransparent};
  transition: all 0.3s ease;
  opacity: 0.9;
  ${props => props.animated ? `animation: ${jump} 1.3s ease infinite alternate` : ''};

  ${props => props.animationFill ? `
    &:hover {
      transform: scale(1.1);
      color: ${colors.darkNavy};
    }

    &:before {
      content: "";
      position: absolute;
      background: ${colors.whiteTransparent};
      bottom: 0;
      top: 0;
      right: 0;
      left: 100%;
      z-index: -1;
      transition: left 0.3s ease-in;
    }

    &:hover:before {
      left: 0;
    }
  `
  : `
    &:hover {
      opacity: 1;
      transform: scale(1.1);
      background-color: ${props.inverted ? colors.darkNavy : '#fff'};
      ${!props.inverted && `color: ${colors.darkNavy};`}
      ${!props.inverted && `border-color: ${colors.darkNavy};`}
    }
  `
  }


  @media (max-width: ${mobileThresholdPixels}) {
    padding: 20px 25px;
    font-size: 14px;
    margin-top: 15px;
    min-width: 220px;
  }
`;

export const ButtonHeaderContainer = styled.div`
  position: absolute;
  bottom: 100px;
  width: 100%;
  text-align: center;

  @media (max-width: ${mobileThresholdPixels}) {
    position: static;
  }
`;

export const Title2 = styled.h2`
  text-align: center;
  width: 100%;
  color: ${colors.navy};
  font-size: ${props => props.small ? '36px' : '48px'};
  font-weight: 300;

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: ${props => props.small ? '18px' : '20px'};
  }
`;

export const Title3 = styled.h3`
  text-align: center;
  width: 100%;
  color: ${colors.navy};
  font-size: 24px;
  font-weight: 300;
  margin: 0px;

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 20px;
    margin-top: 20px;
  }
`;

export const Banner = styled.div`
  background-image: url(${trait});
  background-repeat: repeat-x;
  height: 60px;
  width: ${props => props.width || '100vw'};
`;

export const Icon = styled.img`
  height: 80px;
  width: 80px;
  margin: 20px;

  &:hover{
    transform: scale(1.1) rotate(-7deg);
  }

  @media (max-width: ${mobileThresholdPixels}) {
    margin: 10px;
  }
`;



