import React, { PropTypes } from 'react';
import styled from 'styled-components';
import { colors, mobileThresholdPixels } from '../styledComponents';
import { Icon, Title3 } from './homeStyledComponents';

const Container = styled.div`
  margin: 10px auto;
  flex: 1;
  text-align: center;
  max-width: 300px;
`;

const Number = styled.span`
  padding: 10px 25px;
  color: ${colors.navy};
  text-align: center;
  font-size: 40px;
  font-weight: 300;
  border-radius: 80px;
  border: 1px solid #333;

  &:hover {
    border-width: 2px;
    font-weight: bold;
  }
`;

const NumberContainer = styled.div`
  margin: 20px;
`;

const Body = styled.div`
  margin-top: 20px;

  @media (max-width: ${mobileThresholdPixels}) {
    margin-top: 0px;
  }
`;

const NumerotedBlock = ({ number, children, icon, title }) => (
  <Container>
    {title && <Title3>{title}</Title3>}
    {icon ?
      <Icon src={icon} alt="" />
      : <NumberContainer><Number>{number}</Number></NumberContainer>
    }
    <Body>{children}</Body>
  </Container>
);

NumerotedBlock.propTypes = {
  icon: PropTypes.string,
  number: PropTypes.number,
  children: PropTypes.node.isRequired,
  title: PropTypes.string,
};

NumerotedBlock.defaultProps = {
  icon: '',
  number: undefined,
  title: '',
};

export default NumerotedBlock;



