import { pricingInverse } from '../../pricing';

// Initial State
const initialState = { orderItems: [], total: 0 };

const OrderReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'ORDER.ADD_ITEM':
      return {
        ...state,
        orderItems: [...state.orderItems, action.item],
      };
    case 'ORDER.TOGGLE_ALTERATION': {
      const { itemIndex, alterationId } = action;
      const newItem = { ...state.orderItems[itemIndex] };
      newItem.alterations = newItem.alterations || {};
      newItem.alterations[alterationId] = !newItem.alterations[alterationId];
      const newItems = [...state.orderItems];
      newItems[itemIndex] = newItem;

      const alterationPrice = pricingInverse[alterationId].price;
      const newTotal = newItem.alterations[alterationId] ?
        (state.total + alterationPrice) :
        (state.total - alterationPrice);
      return { ...state, orderItems: newItems, total: newTotal };
    }
    default:
      return state;
  }
};

/* Selectors */

// Get all orders
export const getOrderItems = state => state.order.orderItems;

// Export Reducer
export default OrderReducer;



