import React from 'react';
import styled from 'styled-components';
import { Banner, Title2 } from '../Home/homeStyledComponents';
import { colors, mobileThresholdPixels } from '../styledComponents';
import photo from '../../assets/tilliste2.jpg';

const Container = styled.div`
  position: relative;
`;

const InvertedBlock = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px 0px;

  @media (max-width: ${mobileThresholdPixels}) {
    flex-direction: column;
    padding: 2px 0px;
  }
`;

const Column = styled.div`
  margin: 20px;
  max-width: 45%;
  display: flex;
  flex-direction: column;
  text-align: center;

  @media (max-width: ${mobileThresholdPixels}) {
    margin: 2px;
    max-width: 100%;
  }
`;

const P = styled.p`
  color: ${colors.navy};
`;

const Img = styled.img`
  object-fit: cover;
  height: 400px;
`;

const BannerContainer = styled.div`
  position: absolute;
  top: 40px;
`;

const Padding = styled.div`
  height: 60px;
`;

const TheWhat = () => (
  <Container id="TheWhat">
    <InvertedBlock>
      <Column>
        <Img src={photo} alt="" />
      </Column>
      <Column>
        <Padding />
        <Title2 small>{" tre couturier c'est "}</Title2>
        <P>{"tre diplm dans le secteur de la couture ou avoir au moins 5 ans d'exprience."}</P>
        <P>  </P>
        <P>{'tre travailleur indpendant : avoir le statut auto-entrepreneur, par exemple.'}</P>
        <P>  </P>
        <P>{'tre majeur et avoir un smartphone (qui marche).'}</P>
      </Column>
    </InvertedBlock>
    <BannerContainer><Banner /></BannerContainer>
  </Container>
);

export default TheWhat;



