import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router';
import { Title2, Button, Banner } from './homeStyledComponents';
import { colors, mobileThresholdPixels } from '../styledComponents';

const Container = styled.div`
  margin: 0px 20px;
  padding: 20px 0px 40px;
  display: flex;
  flex-direction: row;

  @media (max-width: ${mobileThresholdPixels}) {
    flex-direction: column;
  }
`;

const P = styled.p`
  color: ${colors.navy};

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 12px;
    text-align: justify;
  }
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
`;

const BannerContainer = styled.div`
  position: absolute;
  top: 90px;
  @media (max-width: ${mobileThresholdPixels}) {
    display: none;
  }
`;

const LeftBlockContainer = styled.div`
  width: 100%;
  position: relative;
`;

const ButtonContainer = styled.div`
  @media (max-width: ${mobileThresholdPixels}) {
    text-align: center;
  }
`;

const BeTilliste = () => (
  <Container id="BeTilliste">
    <LeftBlockContainer>
      <Title2> devenez couturier </Title2>
      <BannerContainer>
        <Banner width="50vw" />
      </BannerContainer>
    </LeftBlockContainer>
    <Column>
      <P>
        {'Vous tes diplm dans le secteur de la couture ou vous avez cinq d\'exprience professionnelle,'}
        {'rejoignez notre quipe de couturiers'}
      </P>
      <P>  </P>
      <Link to="/devenir-couturier">
        <ButtonContainer>
          <Button animationFill inverted style={{ margin: '0' }}>{'> je veux tre couturier <'}</Button>
        </ButtonContainer>
      </Link>
    </Column>
  </Container>
);

export default BeTilliste;



