import React from 'react';
import styled from 'styled-components';
import { colors, mobileThresholdPixels } from '../styledComponents';
import logo from '../../assets/logo.png';

const Container = styled.div`
  padding: 40px 0px 40px;
  background-color: ${colors.lightGrey};
  text-align: center;
`;

const A = styled.a`
  color: ${colors.navy};
  text-decoration: none;
`;

const Row = styled.div`
  color: ${colors.navy};
  text-align: center;
  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 14px;
  }
`;

const Logo = styled.img`
  max-width: 1000px;
  width: 100%;
`;

const Footer = () => (
  <Container>
    <Logo src={logo} alt="Logo de tilli votre couturier  domicile" />
    <Row>
      <A
        target="_blank"
        rel="noreferrer noopener"
        href="https://uploads.strikinglycdn.com/files/dbf14d7e-ff32-4882-a64c-c121e9a1d67e/CGUCGVtilli.pdf"
      >
        {'nos CGU/CGV '}
      </A>
      |
      <A
        target="_blank"
        rel="noreferrer noopener"
        href="https://www.mangopay.com/terms/end-user-terms-and-conditions/Mangopay_Terms-FR.pdf"
      >
        {' CGU de Mangopay '}
      </A>
      |
      <A
        target="_blank"
        rel="noreferrer noopener"
        href=""
      >
        {' mentions lgales '}
      </A>
      | tous droits rservs
    </Row>
  </Container>
);

export default Footer;



